import React from 'react';
import TimeAgo from 'react-timeago';
import f from '../../female.png';
import m from '../../male.png';
import './userprofile.css';

const userProfile = ({k, iu,checkFollow,isFollowing,followClick,userFollowing}) => {
let isFollow=false	
return(
<div className='stc'>{iu===undefined?<div className='userprofile shadow-5 ma3 bg-white pa3 ba-black-80 center'></div>:
<div key={k}>
{userFollowing.map((follow,i)=>{
  	 	if(userFollowing[i].following===iu.email){
  	 	isFollow=true;
  	 }
  	 })}
<div className='bg-white ba-black-80 center  h7'>
<h1 className='App-title f3 u tc'>Featuring Student</h1>
<p><img alt='abc' className='fr bg-light-green br3 pa3 ma3 mt0 grow shadow-5 w-20-l w-20-m w-30'  src={`https://robohash.org/${iu.id}?200*200`}/></p>
<div key={k+32} >{isFollow===false?<button className='w3 h2 bg-blue white' onClick={()=>{followClick(true)}}>Follow</button>:<button className='w3 h2 bg-blue white' onClick={()=>{followClick(false)}}>Unfollow</button>}
</div>
<div><p className='f3-m f3-l f4 b '>Name:{iu.name}</p>
<p className='f3-m f3-l f4 b '>branch:{iu.branch}</p>
<p className='f3-m f3-l f4 b '>year:{iu.year}</p>
<p className='f3-m f3-l f4 b '>Gender:{iu.gender==='f'?<img src={f} height='19em' className='pa1 pb0'/>:<img src={m} height='19em' className='pa1 pb0'/>}</p>
<p className='f3-m f3-l f4 b '>Followers: {iu.followers}</p>
<p className='f3-m f3-l f4 b '>joined:<TimeAgo date={iu.joined}  /></p>
</div>
</div>
</div>}
</div>
	)
	}
export default userProfile;


