import React from 'react'
import UserProfile from './userprofile/userprofile'
import Model from '../../Model/Model';

const Profile=(props)=>{
	return(
		<Model show={props.show} modalClosed={props.modalClosed}>
		<UserProfile k={props.k} users={props.users} iu={props.iu} followClick={props.followClick} userFollowing={props.userFollowing} checkFollow={props.checkFollow} isFollowing={props.isFollowing}/>
		</Model>
		)}
		export default Profile;


